                        </tr>
                        <tr>
                          <th>信用評等</th>
                          <td></td>
                        </tr>
                        <tr>
                          <th>國別</th>
                          <td></td>
                        </tr>
                        <tr>
                          <th>子公司否</th>
                          <td></td>
                        </tr>
                        <tr>
                          <th>業務窗口</th>
                          <td></td>
                        </tr>
                        <tr>
                          <th>客戶聯絡人</th>
                          <td></td>
                        </tr>
                    </table>
                  </div>
                
              </div>
              <div class="slideBox">
                <div class="slideTitle" @click="openSlide(2)">Order
                  <img src="@/assets/images/icon-plus.svg" v-if="slideStatus_2 == ''" height="20" alt="">
                  <img src="@/assets/images/icon-less.svg" v-if="slideStatus_2 != ''" height="20" alt="">
                </div>
                
                <div class="slideContent" v-if="slideStatus_2=='active'">
                  <table class="table_type_2">
                    <thead>
                      <tr>
                        <th>日期</th>
                        <th>訂單單號</th>
                        <th>總數量</th>
                        <th>總金額</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                      </tr>
                    </tbody>
                  </table>
                </div>
                
              </div>
              <div class="slideBox">
                <div class="slideTitle" @click="openSlide(3)">AR 
                  <img src="@/assets/images/icon-plus.svg" v-if="slideStatus_3 == ''" height="20" alt="">
                  <img src="@/assets/images/icon-less.svg" v-if="slideStatus_3 != ''" height="20" alt="">
                </div>
                
                <div class="slideContent" v-if="slideStatus_3=='active'">
                  <table class="table_type_2">
                    <thead>
                      <tr>
                        <th>AR單號</th>
                        <th>帳款日期</th>
                        <th>應收金額</th>
                        <th>已沖金額</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                      </tr>
                    </tbody>
                  </table>
                </div>
                
              </div>
              <div class="slideBox">
                <div class="slideTitle" @click="openSlide(4)">Rebate
                  <img src="@/assets/images/icon-plus.svg" v-if="slideStatus_4 == ''" height="20" alt="">
                  <img src="@/assets/images/icon-less.svg" v-if="slideStatus_4 != ''" height="20" alt="">
                </div>
                
                <div class="slideContent" v-if="slideStatus_4=='active'">
                  <table class="table_type_2">
                    <thead>
                      <tr>
                        <th>銷退單號</th>
                        <th>銷退日期</th>
                        <th>出貨單號</th>
                        <th>銷退金額</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                      </tr>
                    </tbody>
                  </table>
                </div>
                
              </div>
              <div class="slideBox">
                <div class="slideTitle" @click="openSlide(5)">Sell Out
                  <img src="@/assets/images/icon-plus.svg" v-if="slideStatus_5 == ''" height="20" alt="">
                  <img src="@/assets/images/icon-less.svg" v-if="slideStatus_5 != ''" height="20" alt="">
                </div>
                
                <div class="slideContent table-responsive" v-if="slideStatus_5=='active'">
                  <table class="table_type_2">
                    <thead>
                      <tr>
                        <th></th>
                        <th>W01</th>
                        <th>W02</th>
                        <th>W03</th>
                        <th>W04</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td>Sell out</td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                      </tr>
                      <tr>
                        <td>Inv.</td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                      </tr>
                    </tbody>
                  </table>
                </div>
                
              </div>
              <div class="slideBox">
                <div class="slideTitle" @click="openSlide(6)">近四週庫存週數
                  <img src="@/assets/images/icon-plus.svg" v-if="slideStatus_6 == ''" height="20" alt="">
                  <img src="@/assets/images/icon-less.svg" v-if="slideStatus_6 != ''" height="20" alt="">
                </div>
                
                <div class="slideContent" v-if="slideStatus_6=='active'">
                  <table class="table_type_2">
                    <thead>
                      <tr>
                        <th></th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td></td>
                      </tr>
                    </tbody>
                  </table>
                </div>
                
              </div>
             
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Carousel, Slide } from 'vue-carousel';
import Chart from 'chart.js'
var _chart01;
var _chart02;

export default {
  components: {
    Carousel,
    Slide
  },
  props: {

  },
  watch: {
    // pageData(newCount, oldCount) {
    //   if(newCount != oldCount){
    //     this.setChart();
    //   }
    // }
        
  },
  computed: {
    subpage_status() {
      return this.$store.state.subpage_status;
    },
    subPageId(){
      return this.$store.state.subPageId
    },
    subPageDataId(){
      return this.$store.state.subPageDataId
    },
    pageData(){
      if(this.$store.state.customerProfileData[this.$store.state.mainId].tableData != null){
        return this.$store.state.customerProfileData[this.$store.state.mainId].tableData[this.$store.state.pageId].pageData[2][this.$store.state.subPageId][this.$store.state.subPageDataId]
      }else{
        return null
      }
    },
  },
  mounted:function(){
    this.initHeight();
    window.onresize = () => {
      this.initHeight();
    }
    this.setChart();
  },
  data(){
    return{
      watermark: this.$store.state.watermark,
      //狀態
      slideStatus_1: 'active',
      slideStatus_2: '',
      slideStatus_3: '',
      slideStatus_4: '',
      slideStatus_5: '',
      slideStatus_6: '',

      //data 1
      baseInfoData:{
        id: '',
        level: '',
        country: '',
        company: '',
        business:'',
        customerContact:''
      },
      orderData:[]

    }
  },
  methods:{
    initHeight(){
      var h = window.innerHeight;
      var hh = window.innerHeight - 50;
      document.getElementById('content-div-subpage').setAttribute("style","height:"+h+"px");
      document.getElementById('subPage-div').setAttribute("style","height:"+hh+"px");
    },
    pageBack(){
      this.$store.commit('closeSubPage');
    },
    setChart(){
      
      if(_chart01){
        _chart01.destroy();
      }
      if(_chart02){
        _chart02.destroy();
      }
      
      let chartColor = this.$store.state.chartColor;
    
      var ctx01 = document.getElementById('subPageChart01');
      _chart01 = new Chart(ctx01, {
          type: 'doughnut',
          data: {
              labels: ['aaa','bbb','ccc'],
              datasets: [{
                  label: ['aaa','bbb','ccc'],
                  data: ['70','15','15'],
                  backgroundColor: chartColor,
                  borderColor: chartColor,
                  borderWidth: 0,
              }],
          },
          options: {
            aspectRatio: 2,
            legend: {
              labels: {
                  width: 50,
                  fontColor: '#fff',
                  fontFamily: 'Microsoft JhengHei'
              },
              position : "left"
            },
          }
      });

      var ctx02 = document.getElementById('subPageChart02');
      _chart02 = new Chart(ctx02, {
          type: 'line',
          data: {
              labels: ['W01','W02','W03'],
              datasets: [{
                  label: '今年全球實銷: NTXXXXX',
                  data: ['700000','450000','670000'],
                  pointBorderColor: "#fff",
                  pointBackgroundColor: "#ff6384",
                  backgroundColor: 'rgba(255,255,255,0.1)',
                  borderWidth: 2,
                  borderColor: '#ff6384',
                  lineTension: 0 //使用折線 不要曲線
              }],
          },
          options: {
            aspectRatio: 2,
            legend: {
              labels: {
                  width: 50,
                  fontColor: '#fff',
                  fontFamily: 'Microsoft JhengHei'
              },
              position : "top"
            },
            
            scales: {
                yAxes: [{
                    ticks: {
                        beginAtZero: true,
                        fontColor: '#fff',
                        fontFamily: 'Microsoft JhengHei'
                    },
                }],
                xAxes: [{
                    ticks: {
                        beginAtZero: true,
                        fontColor: '#fff',
                        fontFamily: 'Microsoft JhengHei'
                    },
                }]
            }
          }
      });
        
        // let _this = this;
        // ctx01.onclick = function(e) {
        //   var slice = _chart01.getElementAtEvent(e);
        //   if (!slice.length) return; 
        //   console.log(slice[0]._index);
          
        //   _this.$store.state.subPageId = slice[0]._index;
        // }
    },
    openSlide(id){
      switch(id){
        case 1:
          if(this.slideStatus_1 == ''){
            this.slideStatus_1 = "active";
          }else{
            this.slideStatus_1 = "";
          }
          break;
        case 2:
          if(this.slideStatus_2 == ''){
            this.slideStatus_2 = "active";
          }else{
            this.slideStatus_2 = "";
          }
          break;
        case 3:
          if(this.slideStatus_3 == ''){
            this.slideStatus_3 = "active";
          }else{
            this.slideStatus_3 = "";
          }
          break;
        case 4:
          if(this.slideStatus_4== ''){
            this.slideStatus_4 = "active";
          }else{
            this.slideStatus_4 = "";
          }
          break;
        case 5:
          if(this.slideStatus_5 == ''){
            this.slideStatus_5 = "active";
          }else{
            this.slideStatus_5 = "";
          }
          break;
        case 6:
          if(this.slideStatus_6 == ''){
            this.slideStatus_6 = "active";
          }else{
            this.slideStatus_6 = "";
          }
          break;
        default:
          this.slideStatus_1 = "";
          this.slideStatus_2 = "";
          this.slideStatus_3 = "";
          this.slideStatus_4 = "";
          this.slideStatus_5 = "";
          this.slideStatus_6 = "";
      }
      
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped src="@/assets/css/page.css"></style>
<style scoped>
.content-div-subpage{
  position: fixed;
  z-index: 10;
  top: 0;
  left: 100%;
  width: 100%;
  height: 100vh;
  overflow-y: hidden;
  overflow-x: hidden;
  /* background: #212335; */
  background: #43648B;
    transition: 0.3s all;
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    -ms-transform: translateZ(0);
    -o-transform: translateZ(0);
    transform: translateZ(0);
  /* background: linear-gradient(90deg,#212335,#4e505f,#212335); */
  /* background: linear-gradient(180deg, #43648B 0%, #6FA1D1 48.44%, #2F76B8 100%); */
  background: linear-gradient(270deg, #43648B 0%, #6FA1D1 48.44%, #2F76B8 100%);
}

.content-div-subpage.active{
  left: 0;
}

.subPage-div{
  position: absolute;
