    position: relative;
    display: inline-block;
    width: 70px;
    text-align: center;
    line-height: 18px;
    font-size: 14px;
    /* font-weight: 700; */
    color: rgba(255,255,255,0.05);
    /* transform: rotate(30deg); */
}

/* #subPageChart01,
#subPageChart02{
  width: 100%;
} */


.window-box-cover{
  position: fixed;
  z-index: 999;
  left: 0;
  top: 0px;
  width: 100%;
  height: calc(100% - 0px);
  background: rgba(0,0,0,0.7);
}
.window-box{
  position: relative;
  margin: 0 auto;
  margin-top: calc(50vh - 25vh);
  /* display: inline-block; */
  width: calc(100% - 30px);
  max-width: 500px;
  background: rgba(62, 65, 79,1);
  border-radius: 5px;
  padding: 15px;
  min-height: 50vh;
}

.header{
    position: absolute;
    width: 100%;
    height: 50px;
    top: 0;
    left: 0;
    z-index: 1;
    /* background: #212335; */
    background: #0f101b;
    padding: 0;
    line-height: 50px;
    color: #fff;
    text-align: center;
  }

  .pageBack-btn{
    position: absolute;
    left: 0;
    top: 0;
    width: 50px;
    height: 50px;
    text-align: center;
    line-height: 50px;
    cursor: pointer;
    transition: 0.2s all;
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    -ms-transform: translateZ(0);
    -o-transform: translateZ(0);
    transform: translateZ(0);
  }

  .pageBack-btn:hover{
    background: #4e505f;
  }

  .pageBack-btn img{
    transform: rotate(-90deg);
  }
.slideBox{
  position: relative;
  width: 100%;
  height: auto;
}
.slideTitle{
  position: relative;
  width: 100%;
  height: auto;
  min-height: 40px;
  line-height: 40px;
  background: rgba(0,0,0,0.5);
  color: #fff;
  padding: 0 15px;
}

.slideTitle img{
  position: relative;
  float: right;
  margin-top: 10px;
}

.slideContent{
  position: relative;
  width: 100%;
  height: auto;
  min-height: 40px;
  line-height: 40px;
  background: rgba(0,0,0,0.3);
  color: #fff;
  padding: 0 15px;
}

.table_type_1,
.table_type_2{
  width: 100%;
}

.table_type_1 th,
.table_type_1 td{
  border-top: 1px solid #ccc;
}


.table_type_1 tr:nth-child(1) th,
.table_type_1 tr:nth-child(1) td{
  border-top: 0px solid #ccc;
}

.table_type_2 th{
  border-bottom: 2px solid #ccc;
  text-align: center;
}
.table_type_2 td{
  border-top: 1px solid #ccc;
  text-align: center;
}

.my-custom-class .VueCarousel-pagination .VueCarousel-dot-container .VueCarousel-dot{
  width: 30px !important;
  height: 10px !important;
  border-radius: 0 !important;
  
} 

