<template>
  <div class="menu-div" :class="menuOpenStatus">
    <div class="text-center py-2 h3 mb-0" style="border-bottom: 2px solid #333">M E N U</div>
    <div class="menu" v-for="key in menuData" :key="key.id" :class="{'active':key.status}" @click="initStatus(key.id)">{{key.name}}</div>
    <!-- <div class="menu active">Sales Insight</div>
    <div class="menu">Customer Profile</div>
    <div class="menu">Sales & Distribution</div>
    <div class="menu">CPQ</div>
    <div class="menu">KM</div>
    <div class="menu">Marketing</div> -->
  </div>
</template>

<script>
export default {
  props: {
    menuOpenStatus: String,
    // nowstatus: String
  },
  computed: {
    main_status(){
      return this.$store.state.main_status
    },
    menuData(){
      return this.$store.state.menuData
    }
  },
  mounted:function(){
    this.initStatus(this.main_status)
  },
  watch:{
    main_status(newCount, oldCount) {
      if(newCount != oldCount){
        this.initStatus(this.main_status)
      }
    }
	},
  data(){
    return {

    }
  },
  methods:{
    initStatus(id){
      for(let key in this.menuData){
        if(id == this.menuData[key].id){
          this.$store.state.main_status = id;
          this.$store.state.menuData[key].status = true;
        }else{
          this.$store.state.menuData[key].status = false;
        }
      }
      this.changeHome();
    },
    changeHome(){
      
      // this.$emit('nowStatus', this.nowStatus);
      this.$emit('menuOpenStatus', '');
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .menu-div{
    position: fixed;
    left: -250px;
    top: 0;
    height: 100vh;
    width: 250px;
    background: #131313;
    transition: 0.3s all;
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    -ms-transform: translateZ(0);
    -o-transform: translateZ(0);
    transform: translateZ(0);
  }

  .menu-div.active{
    left: 0px !important;
  }
  .menu-div .menu{
    position: relative;
    display: inline-block;
    width: 100%;
    padding: 0 15px;
    transition: 0.2s all;
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    -ms-transform: translateZ(0);
    -o-transform: translateZ(0);
    transform: translateZ(0);
    cursor: pointer;
    height: 60px;
    line-height: 60px ;
    font-weight: 700;
    font-size: 16px;
  }
  .menu-div .menu:hover{
    background: #4e505f;
  }
  .menu-div .menu.active{
    background: #181a2b;
  }

  @media (max-width: 990px) {
    .menu-div{
      width: 300px;
    }
  }

</style>
